import React from 'react'
import TimeCell from './TimeCell'

export default function TimeRow({day, schedule}) {
    return (
        <>
            <td colSpan="2">{day}</td>
            {schedule.map(cell => {
                if (cell.name === "") {
                    console.log(day)
                }
                if (cell.name !== "") {
                    return (<TimeCell name={cell.name} duration={cell.duration}/>)
                }
            })}
        </>
    )
}
